@import url("../css/settings.css");

.islands {
  display: flex;
  flex-direction: column;
  height: 360px;
  margin-bottom: 250px;
}

h2 {
  text-align: center;
  margin: 100px;
  font-size: 36px;
  font-family: Lora;
}

.container {
  margin: 0 100px;
  display: flex;
  justify-content: space-around;
}

.island {
  width: 120px;
  height: 120px;
  background-color: var(--color_red);
  border-radius: 50%;
  position: relative;
}

.island p {
  position: absolute;
  bottom: -75px;
  left: 50%;
  transform: translate(-50%);
  color: var(--color_body_text);
  font-family: Kaushan Script;
  font-size: 32px;
}

.island:first-child::before {
  content: url("../assets/israel\ 1.png");
  position: absolute;
  left: 0;
  top: 3px;
}

.island:nth-child(2)::before {
  content: url("../assets/brazil\ 1.png");
  position: absolute;
  left: 30px;
  top: 33px;
}
.island:nth-child(3)::before {
  content: url("../assets/united-states-of-america\ 1.png");
  position: absolute;
  left: 10px;
  top: 12px;
}
.island:nth-child(4)::before {
  content: url("../assets/japan\ \(1\)\ 1.png");
  position: absolute;
  left: 18px;
  top: 16px;
}
